/**
 * @description startAutoLBS:
wx.onLocationChange( 
    function(res) {
    if(res.errMsg == "auto:location:report:ok"){
        var latitude = res.latitude; // 90 ~ -90
        var longitude = res.longitude; // 180 ~ -180
        var speed = res.speed; // /
        var accuracy = res.accuracy; // 
        var lbsIndex = 0;
    }else {
      //
    }
}
);
 */
import startAutoLBSModule  from "./startAutoLBS";
// const wx = require('../../core/jweixin-1.2.0');

enum locationType {
    wgs84 = 'wgs84',
    gcj02 = 'gcj02',
};

const API_NAME = 'onLocationChange';


const locationChangeModule = {
    locationChangeListener:(callback: WxResponseCallBack, notworkCallback: Function): void => {
        startAutoLBSModule.startAutoLBS({ type: locationType['gcj02'], callback: (result) => {
                if(result.err_msg == "startAutoLBS:ok"){
                    locationChangeModule.onLocationChange(callback);
                }else {
                    notworkCallback && notworkCallback()
                }
        } });
    },
    onLocationChange: (callback: WxResponseCallBack):void => {
        window.wx.onLocationChange(callback);
    },
    getName: (): string => {
        return API_NAME;
    }
}

export default locationChangeModule;