// const wx = require('../../core/jweixin-1.2.0');
type EnumSizeType = {
    original: 'original',
    compressed: 'compressed',
};
type EnumDefaultCameraMode = {
    batch: 'batch',
    normal: 'normal',
};

interface IParam extends WxEvent {
    sizeType?: EnumSizeType[],
    defaultCameraMode?: EnumDefaultCameraMode,
    sourceType: string[]
};

const API_NAME = 'chooseImage';


export default {
    getCamera: (param: IParam) => {
        window.wx.chooseImage(param);
    },
    getName: (): string => {
        return API_NAME;
    }
}
