import axios from "axios";
import storage from "@/utils/storage";

/**
 *  1 ->->: token  wxCard
    2 
    3 
    4
 */
const http = axios.create({ baseURL: window.baseURL || "" });

http.interceptors.request.use(
  function (config) {
    // 
    // config.headers.common["Authorization"] ="Bearer " + storage.getItem("clientToken");
    return config;
  },
  function (error) {
    // 
    return Promise.reject(error);
  }
);

// 
http.interceptors.response.use(
  function (response) {
    // 
    return response.data;
  },
  function (error) {
    // 
    return Promise.reject(error);
  }
);

export default http;
