/**
 * @description 
wx.openLocation({
    latitude: 0, // 90 ~ -90
    longitude: 0, // 180 ~ -180
    name: '', // 
    address: '', // 
    scale: 1, // ,,1~2816
});
 */
// const wx = require('../../core/jweixin-1.2.0');
interface IParam extends WxEvent {
    latitude: number,
    longitude: number,
    name: string,
    address: string,
    scale?: number
};

const API_NAME = 'openLocation';

export default {
    openLocation: (param: IParam) => {
        window.wx.openLocation(param);
    },
    getName: (): string => {
        return API_NAME;
    }
}