// const wx = require("../../core/jweixin-1.2.0");


interface IParam extends WxEvent{
    type?: string
}

const API_NAME = 'getLocation';

export default {
    getLocation: (param: IParam) => {
        window.wx.getLocation(param);
    },
    getName: (): string => {
        return API_NAME;
    }
}